import { WithContext as ReactTags } from 'react-tag-input';
import { v4 as uuid } from 'uuid';
import { MDBCollapse } from 'mdb-react-ui-kit';
import { useState } from 'react';

const KeyCodes = {
  comma: 188,
  enter: 13
};

const randomNumber = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
} 

const Box = (props) => {

  const filteredCards = props.cards.filter((card, index) => card.category === props.boxName);
  const drawnCards = props.drawnCards.filter((card, index) => card.category === props.boxName);

  const addCard = (card) => {
    const newCard = { ...card, id: uuid(), category: props.boxName };
    props.updateCards([...props.cards, newCard])
  }

  const removeCard = (i) => {
    const cardsIndex = props.cards.indexOf(filteredCards[i]);
    props.updateCards(props.cards.filter((card, index) => index !== cardsIndex));
  }

  const drawCard = () => {
    if (filteredCards < 1) {
      return;
    }
    const drawIndex = randomNumber(0,filteredCards.length);
    const card = filteredCards[drawIndex];
    const cardsIndex = props.cards.indexOf(filteredCards[drawIndex]);

    props.updateCards(props.cards.filter((card, index) => index !== cardsIndex));

    setDrawnCard(card);

    const drawnCards = [...props.drawnCards, card];
    props.updateDrawnCards(drawnCards);
  }

  const shuffleCards = () => {
    props.updateCards([...props.cards, ...drawnCards]);
    props.updateDrawnCards(props.drawnCards.filter((card) => card.category !== props.boxName));
    setDrawnCard(false);
  }

  const [show, setShow] = useState(false);

  const toggleShow = () => {
    setShow(!show)
  }

  const [drawnCard, setDrawnCard] = useState();

  const delimiters = [KeyCodes.enter];
  
  return (
    <div>
      <h2 className='d-inline-block'>{props.boxName}</h2>
      <button className='btn btn-primary collapseBtn d-inline-block' onClick={toggleShow}>
        {show ? 'v' : '>'}
      </button>
      <button className='btn btn-primary collapseBtn d-inline-block' onClick={drawCard} disabled={filteredCards.length < 1}>
        Draw ({filteredCards.length})
      </button>
      <button className='btn btn-primary collapseBtn d-inline-block' onClick={shuffleCards} disabled={drawnCards.length < 1}>
        <i className="bi bi-recycle" />
      </button>

      {drawnCard &&
        <div><label className='me-2'>Card: </label>{drawnCard.text}</div>
      }
      <MDBCollapse show={show}>
        <div className={'row mt-1 ' + props.className}>
          <label>Deck</label>
          <ReactTags
            tags={filteredCards}
            delimiters={delimiters}
            handleDelete={removeCard}
            handleAddition={addCard}
            handleDrag={() => { }}
            handleTagClick={() => { }}
            allowDeleteFromEmptyInput={false}
            placeholder="Add a card"
            inputFieldPosition="top"
            allowUnique={false}
          />
        </div>
        <div className={'row mt-1 ' + props.className}>
          <label>Pile</label>
          <ReactTags
            tags={drawnCards}
            readOnly={true}
            allowDeleteFromEmptyInput={false}
            placeholder="Add a card"
          />
        </div>
      </MDBCollapse>
      <hr/>
    </div>
  );
}

export default Box;