import { WithContext as ReactTags } from 'react-tag-input';
import { MDBCollapse } from 'mdb-react-ui-kit';
import { useState } from 'react';

const KeyCodes = {
  comma: 188,
  enter: 13
};

const Boxes = (props) => {

  const handleDelete = i => {
    props.updateBoxes(props.boxes.filter((box, index) => index !== i));
  }

  const handleAdd = box => {
    props.updateBoxes([...props.boxes, {...box, collapsed: false}]);
  }

  const handleDrag = (tag, currPos, newPos) => {
    const newBoxes = props.boxes.slice();

    newBoxes.splice(currPos, 1);
    newBoxes.splice(newPos, 0, tag);

    props.updateBoxes(newBoxes);
  };

  const handleBoxClick = index => {
    console.log('The box at index ' + index + ' was clicked');
  };

  const [show, setShow] = useState(false);

  const toggleShow = () => {
    setShow(!show)
  }

  const delimiters = [KeyCodes.enter];

  return (
    <div className='mt-2'>
      <h2 className='d-inline-block'>Decks</h2>
      <button className='btn btn-primary collapseBtn d-inline-block' onClick={toggleShow}>
        {show ? 'v' : '>'}
      </button>
      <MDBCollapse show={show}>
        <ReactTags
          tags={props.boxes}
          delimiters={delimiters}
          handleDelete={handleDelete}
          handleAddition={handleAdd}
          handleDrag={handleDrag}
          handleTagClick={handleBoxClick}
          allowDeleteFromEmptyInput={false}
          placeholder="Add a box"
          inputFieldPosition="top"
        />
      </MDBCollapse>
    </div>
  );
}

export default Boxes;