import './styles/App.scss';
import {Component} from 'react';
import NavBar from './components/NavBar';
import Boxes from './components/Boxes';
import Box from './components/Box';
import Notes from './components/Notes';

class App extends Component {

  constructor() {
    super();
    this.state = {
      cards: [],
      drawnCards: [],
      boxes: [],
      notes: ''
    };
    this.dragId = '';
    window.importData = this.importData;
    window.exportData = this.exportData;
    window.wipeData = this.wipeData;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state !== this.prevState) {
      localStorage.setItem('cards-data', JSON.stringify(this.state));
    }
  }

  componentDidMount() {
    this.importData(localStorage.getItem('cards-data'));
  }

  importData = (str) => {
    const data = JSON.parse(str, (key, value) => {
      return value;
    });
    if (data) {
      this.setState(() => {
        return { drawnCards: data.drawnCards, cards: data.cards, boxes: data.boxes, notes: data.notes }
      });
    } else {
      this.setState(() => {
        return { cards: [], boxes: [] }
      });
    }
  }

  updateBoxes = (boxes) => {
    this.setState(()=>{
      return { boxes };
    });
  }

  updateCards = (cards) => {
    this.setState(()=> {
      return { cards };
    });
  }

  updateDrawnCards = (drawnCards) => {
    this.setState(() => {
      return { drawnCards };
    });
  }

  updateNotes = (notes) => {
    this.setState(()=> {
      return { notes };
    });
  }

  render() {
    return (
      <div className="App">
        <NavBar />
        <div className='container-fluid row vh-95'>
          <div className='col-md-6'>
            <Boxes className='mt-4' boxes={this.state.boxes} updateBoxes={this.updateBoxes} />
            <hr />
            {this.state.boxes.map((item) => {
              return (
                <Box
                  key={item.id}
                  boxName={item.text}
                  updateCards={this.updateCards}
                  updateDrawnCards={this.updateDrawnCards}
                  cards={this.state.cards}
                  drawnCards={this.state.drawnCards}
                />
              );
            })}
          </div>
          <div className='col-md-6'>
            <Notes notes={this.state.notes} updateNotes={this.updateNotes}/>
          </div>
        </div>
      </div>
    );
  }

}

export default App;
